'use client'

import { useState } from 'react'
import { Card } from "@/components/ui/card"
import { Button } from "@/components/ui/button"

interface Task {
  id: string
  title: string
  date: string
  status: 'ikke-startet' | 'pågår' | 'fullført'
  priority: 'lav' | 'medium' | 'høy'
}

const defaultTasks: Task[] = [
  {
    id: '1',
    title: 'Takvedlikehold - Bassengvegen 18',
    date: '2024-04-15',
    status: 'ikke-startet',
    priority: 'høy'
  }
]

export default function Planlegging() {
  const [tasks] = useState<Task[]>(defaultTasks)

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'høy': return 'bg-red-100 text-red-800'
      case 'medium': return 'bg-yellow-100 text-yellow-800'
      case 'lav': return 'bg-green-100 text-green-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'fullført': return 'bg-green-100 text-green-800'
      case 'pågår': return 'bg-blue-100 text-blue-800'
      case 'ikke-startet': return 'bg-gray-100 text-gray-800'
      default: return 'bg-gray-100 text-gray-800'
    }
  }

  return (
    <div className="max-w-7xl mx-auto p-4">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-2xl font-bold">Planlegging</h1>
        <Button className="bg-green-600 hover:bg-green-700">
          + Ny oppgave
        </Button>
      </div>

      <div className="grid grid-cols-1 md:grid-cols-3 gap-4">
        {/* Ikke startet */}
        <div className="space-y-4">
          <h2 className="text-lg font-semibold">Ikke startet</h2>
          {tasks.filter(task => task.status === 'ikke-startet').map(task => (
            <Card key={task.id} className="p-4">
              <h3 className="font-medium">{task.title}</h3>
              <p className="text-sm text-gray-500">Dato: {task.date}</p>
              <div className="mt-2 flex gap-2">
                <span className={`px-2 py-1 rounded text-xs ${getPriorityColor(task.priority)}`}>
                  {task.priority}
                </span>
                <span className={`px-2 py-1 rounded text-xs ${getStatusColor(task.status)}`}>
                  {task.status}
                </span>
              </div>
            </Card>
          ))}
        </div>

        {/* Pågår */}
        <div className="space-y-4">
          <h2 className="text-lg font-semibold">Pågår</h2>
          {tasks.filter(task => task.status === 'pågår').map(task => (
            <Card key={task.id} className="p-4">
              <h3 className="font-medium">{task.title}</h3>
              <p className="text-sm text-gray-500">Dato: {task.date}</p>
              <div className="mt-2 flex gap-2">
                <span className={`px-2 py-1 rounded text-xs ${getPriorityColor(task.priority)}`}>
                  {task.priority}
                </span>
                <span className={`px-2 py-1 rounded text-xs ${getStatusColor(task.status)}`}>
                  {task.status}
                </span>
              </div>
            </Card>
          ))}
        </div>

        {/* Fullført */}
        <div className="space-y-4">
          <h2 className="text-lg font-semibold">Fullført</h2>
          {tasks.filter(task => task.status === 'fullført').map(task => (
            <Card key={task.id} className="p-4">
              <h3 className="font-medium">{task.title}</h3>
              <p className="text-sm text-gray-500">Dato: {task.date}</p>
              <div className="mt-2 flex gap-2">
                <span className={`px-2 py-1 rounded text-xs ${getPriorityColor(task.priority)}`}>
                  {task.priority}
                </span>
                <span className={`px-2 py-1 rounded text-xs ${getStatusColor(task.status)}`}>
                  {task.status}
                </span>
              </div>
            </Card>
          ))}
        </div>
      </div>
    </div>
  )
} 